import axios from "axios";
import React, { useEffect } from "react";
import "./GenreSlide.css";

const GenreSlide = ({
  genress,
  setGenress,
  selectedGenres,
  setSelectedGenres,
  setPageNo,
  type,
  movieResults
}) => {
  
 async function updataData () {
  try {
      await axios
      .get(`https://api.themoviedb.org/3/genre/movie/list?api_key=8715e8842217df4604773f0cef2fca91&language=en-US`)
      .then(( {data}) => {
        setGenress(data.genres);
        // setTotalPage(data.total_pages);
        console.log(data );
      })
  } catch (err) {
      // Handle Error Here
      console.error(err);
  }
};
  useEffect(() => {
    updataData();
    // eslint-disable-next-line
   }, []);
  return (
    <>
      <div className="chip">
        {movieResults && genress.map((g)=>(
          <div className="name" key={g.id}>{g.name}</div>
        ))}
      </div>
    </>
  );
};

export default GenreSlide;
